@media screen and (min-width: 768px) {
    .main-content {
        display: grid;

        grid-gap: 20px;
        grid-template-columns: 80% 20%;
    }
}

@media screen and (max-width: 1200px) {
    .header-nav__menu .header-nav__link {
        font-size: 0.7em;

    }

    .header-nav__menu .header-submenu .header-submenu__link {
        font-size: 0.7em;
    }

    .header-nav__menu .header-submenu {
        bottom: -220px;
        width: 200px;
    }

    .header-nav__menu .header-submenu .header-submenu__item:hover .lvl2 {
        bottom: -30px;
        left: 100px;
    }



}

@media screen and (max-width: 993px) {
    .header-nav__menu {
        padding: 0;

        .header-nav__item {
            padding: 0;
        }

        .header-submenu {
            bottom: -200px;
        }
    }



}


@media screen and (max-width: 768px) {

    .icon-menu {
        display: block;
    }
    .header-nav__menu{
        display: none;
    }
    .header .header-content{
        flex-direction: row-reverse;
        .header-content__item{
            width: 40%;
        }
    }

    .header-nav__menu{
        width: 80%;
        .header-submenu {
            width: 130px;
            left: 80px;
        
        }
    }




    
    .header-nav__menu {
        flex-direction: column;
        align-items: space-between;

        .header-nav__item {
            margin-top: 20px;
            margin-bottom: 20px;
            &:not(:last-child) {
                border-right: none;
               


            }
        }


    }

    .main-left {
        flex-direction: column;

        .main-left__item {
            width: 100% !important;
        }
    }

    .sidebar-inner {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}